/**
 *  ui clientdb 
 * title 
 * field 
 * computed  mapfield 
 * tooltip 
 * formatter  ','
  	* ''  
	* ',' 1000  1,000
	* '%'  0.1  10%
	* ':'  90  00:01:30
	* '-'  1655196831390()  2022-06-14
 * stat  groupField  'sum'
  	* 'sum' 
  	* 'avg' 
  	* '-1' 
 * fix >1 <=1 
 * value  ( uni-stat-panel ) todo: 
 * contrast  ( uni-stat-panel ) todo: 
 */

export default [{
	title: '',
	field: 'start_time',
	tooltip: '',
	formatter: '',
	stat: -1
}, {
	title: '',
	field: 'new_device_count',
	tooltip: '',
	value: 0
}, {
	title: '',
	field: 'active_device_count',
	tooltip: '',
	value: 0
}, {
	title: '',
	field: 'page_visit_count',
	tooltip: '',
	value: 0
}, {
	title: '',
	field: 'app_launch_count',
	tooltip: '',
	value: 0
}, {
	title: '',
	field: 'avg_device_session_time',
	computed: 'duration/app_launch_count',
	formatter: ':',
	tooltip: '/',
	value: 0,
	//stat: 'avg'
}, {
	title: ' ',
	field: 'avg_device_time',
	computed: 'duration/active_device_count',
	formatter: ':',
	tooltip: '/',
	value: 0,
	//stat: 'avg'
}, {
	title: '',
	field: 'bounceRate',
	computed: 'bounce_times/app_launch_count',
	formatter: '%',
	tooltip: '',
	value: 0,
	contrast: 0,
	fix: 2
}, {
	field: 'bounce_times',
	disable: true
}, {
	title: '',
	field: 'total_devices',
	tooltip: '',
	value: 0,
}]
