/**
 *  ui clientdb 
 * title 
 * field 
 * computed  mapfield 
 * tooltip 
 * formatter  ','
  	* ''  
	* ',' 1000  1,000
	* '%'  0.1  10%
	* ':'  90  00:01:30
	* '-'  1655196831390()  2022-06-14
 * stat  groupField  'sum'
  	* 'sum' 
  	* 'avg' 
  	* '-1' 
 * fix >1 <1 
 * value  ( uni-stat-panel ) todo: 
 * contrast  ( uni-stat-panel ) todo: 
 */

export default [{
	title: '',
	field: 'path',
	tooltip: '121,2',
	stat: -1
}, {
	title: '',
	field: 'title',
	stat: -1
},
{
	title: '',
	field: 'visit_times',
	tooltip: '',
	value: 0
}, {
	title: '',
	field: 'exit_times',
	tooltip: '(',
	value: 0
}, {
	title: '',
	field: 'exitRate',
	computed: 'exit_times/visit_times',
	formatter: '%',
	tooltip: '',
	// value: 0,
	stat: -1
}, {
	title: '',
	field: 'duration',
	disabled: true,
}, {
	title: '',
	field: 'avg_device_session_time',
	computed: 'duration/visit_times',
	formatter: ':',
	tooltip: '/',
	value: 0
}, {
	title: '',
	field: 'avg_user_time',
	computed: 'duration/visit_devices',
	formatter: ':',
	tooltip: '/',
	value: 0
}, {
	title: '',
	field: 'share_count',
	tooltip: '',
	value: 0
}]
