/**
 *  ui clientdb 
 * title 
 * field 
 * computed  mapfield 
 * tooltip 
 * formatter  ','
  	* ''  
	* ',' 1000  1,000
	* '%'  0.1  10%
	* ':'  90  00:01:30
	* '-'  1655196831390()  2022-06-14
 * stat  groupField  'sum'
  	* 'sum' 
  	* 'avg' 
  	* '-1' 
 * fix >1 <1 
 * value  ( uni-stat-panel ) todo: 
 * contrast  ( uni-stat-panel ) todo: 
 */

function fieldsFactory(maps = [{
	title: '',
	field: 'new_device_count',
	stat: 0
}]) {
	let fieldsMap = [{
		title: '',
		field: 'start_time',
		tooltip: '',
		formatter: '-',
		stat: -1
	}]

	if (maps) {
		fieldsMap.push(...maps)
	}

	const values = [1, 2, 3, 4, 5, 6, 7, 14, 30]
	const fields = values.map(val => {
		return {
			title: `${val}`,
			field: `d_${val}`,
			computed: `d_${val}/${maps[0].field}`,
			formatter: '%',
			tooltip: ''
		}
	})

	fieldsMap = fieldsMap.concat(fields)

	return fieldsMap


}

export default fieldsFactory
